.logo {
  display: inline-flex;
  align-items: center;
  text-decoration: none;

  $g: &;

  &:hover {

    #{$g}__img {
      transform: rotate(10deg);
    }

  }

  &__img-wrap {
    min-width: 70px;
    height: 70px;
  }

  &__img {
    width: 70px;
    height: 70px;
    vertical-align: top;
    transition: transform ease-in-out .2s;
  }

  &__text {
    padding-left: 20px;
    font-family: $font-base;
    font-size: 24px;
    color: #fff;

    & span {
      display: block;

      &:nth-child(1) {
        font-weight: 500;
      }

      &:nth-child(2) {
        font-weight: 300;
      }

    }

  }

  &__text-sm {
    display: none;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1200px) {

  .logo {

    &__img-wrap {
      min-width: 60px;
      height: 60px;
    }

    &__img {
      width: 60px;
      height: 60px;
    }

    &__text {
      font-size: 20px;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .logo {

    &__img-wrap {
      min-width: 40px;
      height: 40px;
    }

    &__img {
      width: 40px;
      height: 40px;
    }

    &__text {
      display: none;
    }

    &__text-sm {
      display: block;
      padding-left: 10px;
      font-family: $font-base;
      font-size: 20px;
      color: #fff;
      font-weight: 500;
    }

  }

}

