.footer {
  margin-top: 70px;
  padding-top: 20px;
  padding-bottom: 50px;
  border-top: 1px solid #dde2ec;
}

.footer-nav {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 15px 0 0;

  &__item {
    margin-top: 10px;
    margin-right: 25px;

    &:last-child {
      margin-right: 0;
    }

  }

  &__link {
    font-family: $font-base;
    font-weight: 300;
    font-size: 18px;
    color: $color-description;
    text-decoration: none;
    transition: color ease-in-out .2s;

    &:hover {
      color: $color-link;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .footer {
    margin-top: 50px;
    padding-top: 20px;
    padding-bottom: 50px;
  }

  .footer-nav {
    padding: 15px 0 0;

    &__item {
      margin-top: 10px;
      margin-right: 25px;
    }

    &__link {
      font-size: 16px;
    }

  }

}

