.tag {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;

  &__item {
    padding: 0 5px 5px 0;
  }

  &__link {
    display: inline-block;
    padding: 5px 15px;
    font-family: $font-base;
    font-weight: 300;
    font-size: 16px;
    color: $color-description;
    text-decoration: none;
    background: #f1f2f3;
    border-radius: 50px;
    transition: background ease-in-out .2s;

    &:hover {
      background: #e8e9ea;
    }
  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .tag {

    &__item {

    }

    &__link {
      font-size: 16px;
    }

  }

}
