.header-wrap-bg {
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#3f6db8+0,4877c2+100 */
  background: #3f6db8; /* Old browsers */
  background: -moz-linear-gradient(left, #3f6db8 0%, #4877c2 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #3f6db8 0%, #4877c2 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #3f6db8 0%, #4877c2 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#3f6db8', endColorstr='#4877c2', GradientType=1); /* IE6-9 */
}

.header {
  position: relative;
  display: flex;
  justify-content: space-between;

  &__logo {
    margin-top: 10px;
  }

  &__tool {
    display: flex !important;
  }

  &__user {
    margin-top: 30px !important;
  }

  &__nav-btn {
    display: none;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1200px) {

  .header {
    justify-content: left;

    &__logo {
      margin-top: 0;
      padding: 10px 0;
    }

    &__tool {
      display: none !important;
    }

    &__user {
      display: none !important;
    }

    &__nav-btn {
      display: inline-block;
      margin-right: 25px;
      color: #fff;
      font-size: 24px;
      background: transparent;
      border: none;

      &:focus {
        outline: 3px dotted #EB3F57;
      }

    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1024px) {

  .header {

    &__nav-btn {

      &:focus {
        outline: 0;
      }

    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .header {
    position: fixed;
    top: -1px;
    left: 0;
    z-index: 10;
    width: 100%;
    justify-content: center;
    background: #4270ba;
    //box-shadow: 0px 6px 57px 0px rgba(66, 113, 187, 0.51);

    &__logo {

    }

    &__nav-btn {
      position: absolute;
      left: 0;
      margin-top: 0;
      margin-right: 0;
      padding: 3px 15px 0;
      height: 60px;
      font-size: 20px;
    }

  }

}

