.event-list {
  position: relative;

  &__item {
    display: flex;
    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__date-wrap {
    min-width: 80px;
  }

  &__date {
    display: block;
    padding: 20px 0;
    font-family: $font-base-book;
    font-weight: normal;
    color: #fff;
    background: #eb3f57;
    border-radius: 5px;
    text-align: center;

    & span {
      display: block;
      text-align: center;

      &:nth-child(1) {
        font-size: 40px;
      }

      &:nth-child(2) {
        font-size: 16px;
      }

      &:nth-child(3) {
        margin-top: 5px;
        font-size: 16px;
      }

    }

  }

  &__detail {
    flex-grow: 1;
    padding-left: 20px;
  }

  &__detail-title {
    margin: 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 24px;
    color: $color-title;
  }

  &__detail-link {
    color: inherit;
    transition: color ease-in-out .2s;

    &:hover {
      color: $color-link;
    }

  }

  &__detail-description {
    margin: 10px 0 0 0;
    font-family: $font-base;
    font-weight: 300;
    font-size: 20px;
    color: $color-description;
    line-height: 1.3;
  }

}

