.nav-wrap {

}

.nav {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;

  $g: &;

  &_main {
    padding: 30px 0 25px 0;

    #{$g}__item {
      margin-top: 10px;
    }

  }

  &_user {
    display: none;
  }

  &_tool {
    display: none;

    #{$g}__item {
      padding-top: 27px;
      border-top: 3px solid transparent;

      &_active {
        border-color: #fff;

        #{$g}__link {
          opacity: 1;
        }

      }

    }

    #{$g}__link {
      opacity: .5;

      &:hover {
        opacity: 1;
      }

    }

  }

  &__item {
    margin-right: 25px;

    &:last-child {
      margin-right: 0;
    }

  }

  &__link {
    font-family: $font-base-book;
    font-size: 22px;
    color: #fff;
    text-decoration: none;
    transition: opacity ease-in-out .2s;

    &:hover {
      opacity: .5;
    }

    & [class^="icon-"], & [class*=" icon-"] {
      position: relative;
      top: 3px;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1200px) {

  .nav-wrap {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 99;
    overflow-y: auto;
    transform: translateX(-100%);
    transition: transform .15s cubic-bezier(.4, 0, .2, 1);
    width: 280px;

    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#3f6db8+0,4877c2+100 */
    background: #3f6db8; /* Old browsers */
    background: -moz-linear-gradient(left, #3f6db8 0%, #4877c2 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(left, #3f6db8 0%, #4877c2 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, #3f6db8 0%, #4877c2 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#3f6db8', endColorstr='#4877c2', GradientType=1); /* IE6-9 */

    &.toggled {
      transform: translateX(0);
      transition-duration: 235ms;
    }

  }

  .nav-mask {
    background-color: rgba(0, 0, 0, .5);
    bottom: 0;
    left: 0;
    opacity: 0;
    position: fixed;
    right: 0;
    top: 0;
    transform: translateZ(0);
    transition: opacity .2s linear;
    visibility: hidden;
    z-index: 0;

    &.toggled {
      opacity: 1;
      visibility: visible;
      z-index: 4;
    }

  }

  .nav {

    $g: &;

    &_main {
      padding: 20px 25px;

      #{$g}__item {
        width: 100%;
        margin-bottom: 20px;
      }

      #{$g}__link {

      }

    }

    &_user {
      display: flex;
      margin-top: 20px;
      padding: 20px 25px;
      background: rgba(0, 0, 0, .2);

      #{$g}__item {
        margin-right: 25px;

        &:last-child {
          margin-right: 0;
        }

      }

    }

    &_tool {
      display: flex;
      padding: 0 25px;

      #{$g}__item {
        padding-top: 17px;
        margin-right: 25px;

        &:last-child {
          margin-right: 0;
        }

      }

      #{$g}__link {

      }

    }

    &__item {
      margin-right: 0;

      &:last-child {
        margin-right: 0;
      }

    }

    &__link {
      font-size: 22px;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .nav-wrap {
    z-index: 101;
  }

  .nav-mask {

    &.toggled {
      z-index: 100;
    }

  }

  .nav {
    $g: &;

    &_tool {

      #{$g}__item {
        padding-top: 14px;
      }

    }

    &__link {
      font-size: 18px;
    }

  }

}

