* {
  box-sizing: border-box;
}

html, body {
  position: relative;
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  margin: 0;
  font-family: $font-base;
  letter-spacing: 0.02em;

  //padding-bottom: 700px;

  &.toggled {
    overflow: hidden;
  }

}

a:focus {
  outline: 3px dotted #EB3F57;
}

