.new-arrival-card {
  margin-top: 180px;
}

.new-arrival {
  position: relative;
  display: flex;

  &__img-wrap {
    width: 123px;
    min-width: 123px;
  }

  &__img-link {
    display: inline-block;
    border-radius: 5px;
    overflow: hidden;
  }

  &__img {
    width: 100%;
    vertical-align: top;
  }

  &__detail {
    flex-grow: 1;
    padding-left: 20px;
  }

  &__detail-link {
    text-decoration: none;
  }

  &__detail-title {
    margin: 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 24px;
    color: $color-title;
    text-decoration: underline;
    transition: color ease-in-out .2s;

    &:hover {
      color: $color-link;
    }

  }

  &__detail-description {
    margin: 10px 0 0 0;
    font-family: $font-base;
    font-weight: 300;
    font-size: 20px;
    color: $color-description;
    line-height: 1.2;
  }

}

.news-main {
  margin-top: 160px;
  padding: 20px 25px;
  text-align: center;

  &__title {
    margin: 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 30px;
    color: $color-title;
  }

  &__description {
    margin: 20px 0 0 0;
    font-family: $font-base;
    font-weight: 300;
    font-size: 20px;
    color: $color-description;
    line-height: 1.5;
  }

  &__link {
    display: inline-block;
    margin: 20px 0 0 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 20px;
    color: $color-link;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }

  }

}

// When hide news main
.news-main + .new-arrival-card {
  margin-top: 70px;
}

.news-list {

  &__item {
    margin-bottom: 30px;

    &_mb-big {
      margin-bottom: 50px;
    }

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__date {
    font-family: $font-base;
    font-weight: 300;
    font-size: 16px;
    color: $color-description;
    line-height: 1.5;
  }

  &__title {
    margin: 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 24px;
    color: $color-title;
    line-height: 1.3;
  }

  &__link {
    color: inherit;
    text-decoration: underline;
    transition: color ease-in-out .2s;

    &:hover {
      color: $color-link;
    }

  }

  &__description {
    margin: 10px 0 0 0;
    font-family: $font-base-book;
    font-weight: 300;
    font-size: 20px;
    color: $color-description;
    line-height: 1.2;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1200px) {

  .news-main {
    margin-top: 70px;
  }

}

