.search {

  &__title {
    margin: 90px 0 30px 0;
    font-family: $font-base-book;
    font-size: 40px;
    font-weight: normal;
    color: #fff;
  }

}

.search-input {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #fff;
  border: 2px solid #fff;
  border-radius: 50px;
  box-shadow: 0px 6px 57px 0px rgba(66, 113, 187, 0.21);
  outline: none;
  transition: all ease-in-out .2s;

  &_focus {
    border: 2px solid $color-link!important;
  }

  &_border {
    border: 2px solid #e3e7ef;
    box-shadow: none;
  }

  &_mw {
    max-width: 750px;
  }

  &__select {
    width: 140px;
    padding: 15px 25px;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 20px;
    color: $color-title;
    background: transparent;
    border: none;
    cursor: pointer;
    outline: none;

    -webkit-appearance: none;
    -moz-appearance: none;
    -o-appearance: none;
    appearance: none;
    background-image: url('../images/select/sort-down.svg');
    background-size: 10px 10px;
    background-position: 98% center;
    background-repeat: no-repeat;
    outline: none;

    &::-ms-expand {
      display: none;
    }

  }

  &__input {
    width: 100%;
    flex-grow: 1;
    padding: 17px 0 17px 25px;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 20px;
    color: $color-title;
    background: transparent;
    border: none;
    line-height: 1;
    outline: none;
  }

  &__btn {
    padding: 17px 25px;
    font-size: 20px;
    color: $color-link;
    background: transparent;
    border-radius: 50px;
    border: none;
    outline: none;
  }

}

.search-facet-btn {
  display: none;
}

.search-sub-link {
  display: inline-block;
  margin-top: 30px;
  font-family: $font-base-book;
  font-weight: normal;
  font-size: 24px;
  color: #fff;
  text-decoration: none;
  transition: opacity ease-in-out .2s;

  &:hover {
    opacity: .5;
  }

  & [class^="icon-"], & [class*=" icon-"] {
    font-size: 20px;
    position: relative;
    top: 3px;
    padding-left: 10px;
  }

}

.search-nav {
  display: flex;
  flex-wrap: wrap;
  padding: 20px 0 15px 0;

  &_p-0 {
    padding: 0;
  }

  &_bb {
    border-bottom: 1px solid #dde2ec;
  }

  &__item {
    margin-right: 15px;
    margin-bottom: 5px;
    line-height: 1.2;
  }

  &__title {
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 20px;
    color: $color-description;
  }

  &__link {
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 20px;
    color: $color-link;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }

  }

  &__text {
    margin: 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 20px;
    color: $color-description;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1024px) {

  .search-facet {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 101;
    overflow-y: auto;
    transform: translateX(-100%);
    transition: transform .15s cubic-bezier(.4, 0, .2, 1);
    width: 280px;
    background: #fff;

    &.toggled {
      transform: translateX(0);
      transition-duration: 235ms;
    }

  }

  .search-nav-wrap {
    padding: 15px;
    border: 2px solid #e3e7ef;
    border-radius: 5px;
  }

  .search-nav {

    &_bb {
      border-bottom: 0;
    }

  }

  .search-facet-btn {
    display: block;
    width: 100%;
    padding: 10px 0;
    text-align: center;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 18px;
    color: #fff;
    text-decoration: none;
    background: $color-link;
    border: none;
    border-radius: 50px;
    outline: 0;
    transition: all ease-in-out .1s;

    &.toggled {
      color: $color-title;
      background: #F2F4F8;
    }

  }

  .search-mask {
    background-color: rgba(0, 0, 0, .5);
    bottom: 0;
    left: 0;
    opacity: 0;
    position: fixed;
    right: 0;
    top: 0;
    transform: translateZ(0);
    transition: opacity .2s linear;
    visibility: hidden;

    &.toggled {
      opacity: 1;
      visibility: visible;
      z-index: 99;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .search {

    &_sm-mt {
      margin-top: 25px;
    }

    &__title {
      margin: 30px 0;
      font-size: 24px;
      text-align: center;
    }

  }

  .search-input {

    &__select {
      width: 120px;
      padding: 10px 20px;
      font-size: 18px;
    }

    &__input {
      padding: 12px 0 12px 20px;
      font-size: 18px;
    }

    &__btn {
      padding: 12px 25px;
      font-size: 18px;
    }

  }

  .search-sub-link {
    display: block;
    margin-top: 10px;
    font-size: 20px;
    text-align: center;
  }

  .search-nav-wrap {
    padding: 15px 15px 5px 15px;
    border: 2px solid #e3e7ef;
    border-radius: 5px;
  }

  .search-nav {
    padding: 10px 0 10px 0;

    &_p-0 {

    }

    &_bb {

    }

    &__item {
      margin-right: 15px;
      margin-bottom: 5px;
      line-height: 1.2;

      &_sm-ta-c {
        width: 100%;
        text-align: center;
      }

    }

    &__title {
      font-size: 18px;
    }

    &__link {
      font-size: 18px;
    }

    &__text {
      font-size: 18px;
    }

  }

}

