.calendar {
  width: 100%;
  text-align: center;
  font-family: $font-base-book;
  font-weight: normal;
}
.calendar a {
  font-size: 16px;
  color: $color-description;
  text-decoration: none;
}
.calendar td, .calendar th {
  height: 38px;
}
.calendar .days-week {

}

.calendar .days-week th {
  color: $color-description;
  font-size: 20px;
  font-weight: normal;
}

.calendar .evnt {
  display: block;
  font-weight: normal;
  line-height: 34px;
  //background: #F5F5F6;
  color: $color-link;
  border-radius: 5px;

  &:hover {
    text-decoration: underline;
  }

}

