.card {
  width: 100%;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, .9);
  box-shadow: 0px 6px 57px 0px rgba(66, 113, 187, 0.21);

  &_bs-2 {
    box-shadow: 0px 0px 13px 0px rgba(66, 113, 187, 0.16);
    border: 1px solid #eff4ff;
  }

  &_mt {
    margin-top: 25px;
  }

  &__header {
    padding: 20px 25px 0 25px;
  }

  &__body {
    padding: 20px 25px;
  }

  &__body + &__footer {
    padding: 0;
  }

  &__footer {
    padding: 20px 0 0 0;
  }

  &__title {
    margin: 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 30px;
    color: $color-title;

    &_sm {
      font-size: 24px;
    }

  }

  &__ask-lib-text {
    margin: 0;
    font-family: $font-base;
    font-weight: 300;
    font-size: 20px;
    color: $color-description;
    line-height: 1.5;
  }

  &__text {
    margin: 0;
    font-family: $font-base;
    font-weight: 300;
    font-size: 20px;
    color: $color-description;
    line-height: 1.2;
  }

}

.card-list {
  margin: 0;
  padding: 0;
  list-style: none;

  &__item {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__link {
    display: flex;
    justify-content: space-between;
    font-family: $font-base;
    font-weight: 300;
    font-size: 20px;
    color: $color-link;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }

  }

  &__title {
    display: inline-block;
    color: $color-link;
  }

  &__count {
    display: inline-block;
    margin-left: 20px;
    color: $color-description;
  }

}

.card-nav {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  border-top: 1px solid #dde2ec;

  &__item {
    flex-grow: 1;
    padding: 17px 0 23px 0;
    text-align: center;
    border-right: 1px solid #dde2ec;

    &:last-child {
      border-right: none;
    }

  }

  &__link {
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 20px;
    color: $color-link;
    text-decoration: none;
    background: transparent;
    border: none;

    &:hover {
      text-decoration: underline;
    }

    &:focus {
      outline: 3px dotted #EB3F57;
    }

  }

}

.card-transparent {
  margin-top: 70px;
  width: 100%;

  &__header {
    margin-bottom: 50px;
    display: flex;
  }

  &__title {
    margin: 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 36px;
    color: $color-title;
    line-height: 1;
  }

  &__link {
    display: inline-block;
    margin-top: 11px;
    margin-left: 25px;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 20px;
    color: $color-link;
    text-decoration: none;
    white-space: nowrap;

    &:hover {
      text-decoration: underline;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1200px) {

  .card {
    background-color: #fff;

    &_md-mt {
      margin-top: 70px;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1024px) {

  .card {

    &_facet {
      box-shadow: none;
      border-radius: 0;
      border: 0;
      margin-top: 0!important;

      .card-nav {
        border-bottom: 1px solid #dde2ec;
      }

    }

  }


}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .card {

    &_md-mt {
      margin-top: 50px;
    }

    &__header {
      padding: 20px 25px 0 25px;
    }

    &__body {
      padding: 20px 25px;
    }

    &__footer {
      padding: 20px 0 0 0;
    }

    &__title {
      font-size: 24px;

      &_sm {
        font-size: 20px;
      }

    }

    &__ask-lib-text {
      font-size: 18px;
      line-height: 1.3;
    }

    &__text {
      font-size: 18px;
    }

  }

  .card-list {
    margin: 0;
    padding: 0;
    list-style: none;

    &__item {
      margin-bottom: 10px;

      &:last-child {
        margin-bottom: 0;
      }

    }

    &__link {
      font-size: 18px;
    }

    &__title {

    }

    &__count {

    }

  }

  .card-nav {

    &__item {
      padding: 17px 0 23px 0;
    }

    &__link {
      font-size: 18px;
    }

  }


  .card-transparent {
    margin-top: 50px;

    &__header {
      margin-bottom: 25px;
      flex-direction: column;
    }

    &__title {
      font-size: 24px;
    }

    &__link {
      display: inline-block;
      margin-top: 10px;
      margin-left: 0;
      font-size: 18px;
    }

  }

}

