.list {

  &__item {
    margin-top: 50px;
    display: flex;
  }

  &__col-text {
    flex-grow: 1;
    flex-basis: 0;
    max-width: 100%;
  }

  &__col-img {
    width: 143px;
    min-width: 143px;
    padding-left: 20px;
  }

  &__title {
    margin: 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 24px;
    color: $color-link;
    line-height: 1.2;
    text-decoration: none;
    transition: color ease-in-out .2s;

    &:hover {
      text-decoration: underline;
    }

  }

  &__title-link {
    color: inherit;
    text-decoration: none;
  }

  &__description {
    margin: 10px 0 0 0;
    font-family: $font-base;
    font-weight: 300;
    font-size: 18px;
    color: $color-description;
    line-height: 1.2;
  }

  &__dl {
    margin: 10px 0 0 0;
    font-family: $font-base;
    font-weight: 300;
    font-size: 18px;
    color: $color-description;
    line-height: 1.2;

    & dt {
      float: left;
      clear: left;
      width: 150px;
      padding-right: 20px;
    }

    & dd {
    }

  }

  &__link {
    display: inline-block;
    margin: 10px 0 0 0;
    font-family: $font-base;
    font-weight: 300;
    font-size: 18px;
    color: $color-description;
    text-decoration: underline;
    transition: color ease-in-out .2s;

    &:hover {
      color: $color-link;
    }

  }

  &__img-wrap {
    border-radius: 5px;
    overflow: hidden;
  }

  &__img {
    width: 100%;
    vertical-align: top;
  }

  &__tag {
    margin-top: 10px !important;
  }

}


