.lib-detail {
  padding-top: 10px;

  &__img-wrap {
    position: relative;
    border-radius: 5px;
    overflow: hidden;
    box-shadow: 0px 6px 57px 0px rgba(66, 113, 187, 0.21);
  }

  &__img-wrap-icon {
    display: none;
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    vertical-align: top;
  }

  &__title {
    margin: 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 30px;
    line-height: 1.1;
    color: $color-title;
  }

  &__title-link {
    color: $color-link;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }

  }

  &__title-sub {
    margin: 10px 0 0 0;
    font-family: $font-base;
    font-weight: 300;
    font-size: 20px;
    color: $color-description;
    line-height: 1.2;
  }

  &__link {
    display: inline-block;
    margin: 15px 0 0 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 20px;
    color: $color-link;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

}

.lib-hot-nav {
  display: none;
}

.lib-contact {
  display: flex;
  width: 100%;
  min-height: 200px;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, .9);
  box-shadow: 0px 6px 57px 0px rgba(66, 113, 187, 0.21);

  &__col {
    width: 50%;
    overflow: hidden;
  }

}

.lib-contact-nav {
  margin: 0;
  padding: 20px 25px;
  list-style: none;

  &__item {
    margin-bottom: 10px;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 18px;
    line-height: 1.3;

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__title {
    color: $color-description;
  }

  &__text {
    color: $color-description;
  }

  &__link {
    margin: 10px 0 0 0;
    color: $color-link;
    white-space: nowrap;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }

  }

}

.lib-nav-wrap {
  margin-top: 70px;
  border: 1px solid #dde2ec;
  border-radius: 5px;
}

.lib-nav {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 20px 0;
  list-style: none;
  transition: all ease-in-out .2s;

  &__item {
    width: 100%;
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__link {
    display: block;
    padding: 5px 25px;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 20px;
    color: $color-title;
    text-decoration: none;
    transition: all ease-in-out .2s;

    &:hover {
      color: $color-link;
    }

    &_active {
      background: #eff5ff;
    }

  }

  & ul {
    margin: 0;
    padding: 0;
    list-style: none;

    & li {

    }

    & a {
      display: block;
      padding: 5px 25px;
      font-family: $font-base;
      font-weight: 300;
      font-size: 20px;
      color: $color-link;
      text-decoration: none;

      &:hover {
        text-decoration: underline;
      }

      &.active {
        background: #eff5ff;
      }

    }

  }

}

.lib-banner {
  display: flex;
  flex-wrap: wrap;
  margin-top: 70px;

  &__item {
    margin-right: 1px;
  }

  &__link {

  }

  &__img {
    width: auto;
    max-height: 150px;
    vertical-align: top;
    border-radius: 5px;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1024px) {

  .lib-hot-nav {
    display: flex;
    width: 100%;
    margin: 25px 0 0 0;
    padding: 0;
    list-style: none;

    $g: &;

    &__item {
      width: 50%;
    }

    &__link {
      padding: 10px 0;
      width: 100%;
      text-align: center;
      font-family: $font-base-book;
      font-weight: normal;
      font-size: 18px;
      color: $color-title;
      text-decoration: none;
      background: #F2F4F8;
      border: none;
      outline: 0;
      transition: all ease-in-out .1s;

      &.toggled {
        color: #fff;
        background: $color-link;
      }

    }

  }

  .lib-contact {
    overflow: hidden;
    opacity: 0;
    max-height: 0;
    min-height: auto;
    border: 0;
    border-bottom: 1px solid #dde2ec;
    border-radius: 0;
    box-shadow: none;
    transition: all ease-in-out .2s;

    &__col {

    }

    &.toggled {
      opacity: 1;
      max-height: 10000px;
    }
  }

  .lib-contact-nav {
    padding: 20px 0;
  }

  .lib-nav-wrap {
    margin: 0 -25px;
    overflow: hidden;
    opacity: 0;
    max-height: 0;
    min-height: auto;
    border: 0;
    border-bottom: 1px solid #dde2ec;
    border-radius: 0;
    transition: all ease-in-out .2s;

    &.toggled {
      opacity: 1;
      max-height: 10000px;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .lib-detail {
    padding-top: 50px;

    &__img-wrap {
      border-radius: 0;
      box-shadow: none;
      margin: -51px -15px 0;
    }

    &__img-wrap-icon {
      display: inline-block;
      position: absolute;
      z-index: 5;
      bottom: 15px;
      left: 15px;
      padding: 5px;
      border-radius: 50%;
      background: rgba(0, 0, 0, .5);
      font-size: 16px;
      color: #fff;
      text-align: center;
    }

    &__img {
      position: relative;
      max-height: 100px;
      transition: max-height ease-in-out .2s;

      &.toggled {
        max-height: 1000px;
      }

    }

    &__info {
      margin-top: 15px;
    }

    &__title {
      font-size: 20px;
    }

    &__title-sub {
      margin: 10px 0 0 0;
      font-size: 18px;
    }

    &__link {
      margin: 10px 0 0 0;
      font-size: 18px;
    }

  }

  .lib-contact {
    min-height: auto;
    flex-direction: column;

    &__col {
      width: 100%;
      height: auto;

      &:nth-child(2) {
        height: 200px;
      }

    }

  }

  .lib-contact-nav {

    &__item {
      font-size: 18px;
    }

    &__title {

    }

    &__text {

    }

    &__link {

    }

  }

  .lib-nav-wrap {
    margin: 0 -15px;
  }

  .lib-nav {
    padding: 10px 0;

    &__item {
      width: 100%;
    }

    &__link {
      padding: 5px 15px;
      font-size: 18px;
    }

    & ul {

      & li {

      }

      & a {
        padding: 5px 15px;
        font-size: 18px;
      }

    }

  }

  .lib-banner {
    margin-top: 50px;

    &__item {
      width: 50%;
      margin-right: 0;
      margin-bottom: 0;
    }

    &__link {

    }

    &__img {
      width: 100%;
      max-height: 100%;
    }

  }

}

