/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .content {

    &__title {
      font-size: 24px!important;
    }

    &__info {

      & > span {
        margin: 12px 0;
        font-size: 18px;
        line-height: 1.2;

        & [class^="icon-"], & [class*=" icon-"] {
          top: 1px;
          font-size: 16px;
        }
      }

    }

    &__date {
      margin: 12px 0;
      font-size: 18px;
      line-height: 1.2;

      & [class^="icon-"], & [class*=" icon-"] {
        top: 1px;
        font-size: 16px;
      }

    }

    & h1, & h2, & h3, & h4 {
      font-family: $font-base-book;
      font-weight: normal;
      line-height: 1.2;
    }

    & h1 {
      font-size: 24px;
    }

    & h2 {
      font-size: 22px;
    }

    & h3 {
      font-size: 20px;
    }

    & h4 {
      font-size: 18px;
    }

    & p {
      font-size: 18px;
      line-height: 1.4;
    }

    & a {
      font-size: 18px;
    }

    & img {

    }

    & ul, & ol {
      font-size: 18px;
      line-height: 1.4;
    }

  }

}

