.bg {
  position: absolute;
  top: 0;
  z-index: -1;
  background: url("../images/bg/bg.svg") no-repeat;
  background-size: 4280px 647px;
  background-position: top center;
  width: 100%;
  height: 650px;
}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1200px) {

  .bg {
    top: -80px;
  }

}

@media only screen and (max-width: 1024px) {

  .bg {
    top: 0;
  }

}
