.crumb {
  display: flex;
  padding: 20px 0;
  font-family: $font-base;
  font-size: 18px;
  font-weight: 300;
  overflow: auto;

  &__list {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin: 0;
    padding: 0;
    list-style: none;

    & > li {
      position: relative;
      padding: 0 20px 0 0;

      &:after {
        content: "/";
        font-size: 16px;
        color: $color-title;
        vertical-align: middle;
        position: absolute;
        top: 0;
        right: 5px;
      }

      &:first-child {
        padding-left: 0;
      }

      &:last-child {
        &:after {
          content: "";
        }
      }

      & > span {
        white-space: nowrap;
      }

      & > a {
        color: $color-title;
        text-decoration: none;
        white-space: nowrap;
        transition: all .2s;

        &:hover {
          color: $color-link;
        }

      }

    }

  }

  &__current {
    color: $color-title;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 1024px) {

  .crumb {
    padding: 20px 0;

    &__list {
      flex-wrap: nowrap;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .crumb {
    padding: 10px 0;
    font-size: 14px;
    overflow: auto;

    &__list {
      display: flex;
      width: 100%;

      & > li {
        padding: 0 5px 0 10px;

        &:first-child {
          padding-left: 0;
        }

        &:after {
          content: "/";
          font-size: 12px;
          vertical-align: middle;
          position: absolute;
          top: 0px;
          right: -7px;
        }

        &:last-child {
          &:after {
            content: "";
          }
        }

        & > span {
          white-space: nowrap;
        }

        & > a {
          text-decoration: none;
          transition: all .2s;
          white-space: nowrap;
        }

      }

    }

  }
}


