#map-ymaps {
  width: 100%;
  height: 100%;
}

.map {
  display: flex;
  width: 100%;
  height: 700px;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, .9);
  box-shadow: 0px 6px 57px 0px rgba(66, 113, 187, 0.21);
  overflow: hidden;

  &__col-l {
    width: 500px;
    min-width: 500px;
  }

  &__col-r {
    flex-grow: 1;
    background-color: #FFFCF5;
  }

}

.map-nav {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 20px 25px;

  &__title {
    margin: 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 22px;
    color: $color-title;
  }

  &__help-text {
    margin: 10px 0 0 0;
    font-family: $font-base;
    font-weight: 300;
    font-size: 18px;
    color: $color-description;
    line-height: 1.2;
  }

  &__geo-btn {
    width: 46px;
    height: 46px;
    font-size: 20px;
    color: #fff;
    background: #E22733;
    border-radius: 50px;
    border: none;
    outline: none;
    transition: all ease-in-out .2s;

    &:hover {
      background: $color-link;
    }

    &:focus {
      box-shadow: 0 0 0 0.2rem rgba(77, 121, 201, .5);
    }

  }

}

.map-nav-search {
  display: flex;
  padding-top: 20px;

  &__geo-col {
    width: 46px;
  }

  &__search-col {
    padding-left: 20px;
    flex-grow: 1;
  }

}

.map-nav-search-input {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 750px;
  background: #fff;
  border-radius: 50px;
  border: 1px solid #b4bcc8;
  outline: none;
  transition: all ease-in-out .2s;

  &:focus {
    box-shadow: 0 0 0 0.2rem rgba(235, 63, 87, 1);
  }

  &__input {
    width: 100%;
    flex-grow: 1;
    padding: 7px 0 13px 20px;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 20px;
    color: $color-title;
    background: transparent;
    border: none;
    line-height: 1;
    outline: none;
  }

  &__btn {
    padding: 10px 15px;
    font-size: 20px;
    color: $color-link;
    background: transparent;
    border-radius: 50px;
    border: none;
    outline: none;
  }
}

.map-nav-abc {
  display: flex;
  flex-wrap: wrap;
  margin: -5px;
  padding: 20px 0;
  list-style: none;

  &__item {
    padding: 5px;
  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 46px;
    height: 46px;
    font-size: 20px;
    color: #fff;;
    background: $color-link;
    border-radius: 50px;
    border: none;
    text-decoration: none;
    outline: none;
    transition: all ease-in-out .2s;

    &:hover {
      opacity: .9;
    }

    &:focus {
      box-shadow: 0 0 0 0.2rem rgba(77, 121, 201, .5);
    }
  }

}

.map-nav-result-box {
  flex-grow: 1;
  max-height: 360px;
  border: 1px solid #dde2ec;
  border-radius: 5px;
  overflow-y: auto;
}

.map-nav-result {
  margin: 0;
  padding: 20px;
  list-style: none;

  &__item {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__link {
    display: inline-block;
    margin: 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 20px;
    color: $color-link;
    line-height: normal;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }

    &:first-letter {
      color: #E22733;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1200px) {

  .map {
    height: auto;
    flex-direction: column;

    &__col-l {
      width: 100%;
      min-width: auto;
    }

    &__col-r {
      height: 500px;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .map {

    &__col-l {

    }

    &__col-r {
      height: 350px;
    }

  }

  .map-nav {
    padding: 20px 25px;

    &__title {
      font-size: 20px;
    }

    &__help-text {
      margin: 10px 0 0 0;
      font-size: 16px;
    }

    &__geo-btn {
      width: 40px;
      height: 40px;
      font-size: 18px;
    }

  }

  .map-nav-search {
    padding-top: 15px;

    &__geo-col {
      width: 40px;
    }

    &__search-col {
      padding-left: 15px;
    }

  }

  .map-nav-search-input {

    &__input {
      padding: 6px 0 11px 20px;
      font-size: 18px;
    }

    &__btn {
      padding: 9px 15px 8px;
      font-size: 18px;
    }

  }

  .map-nav-abc {
    margin: -3px;
    padding: 15px 0;

    &__item {
      padding: 3px;
    }

    &__link {
      width: 40px;
      height: 40px;
      font-size: 18px;
    }

  }

  .map-nav-result-box {
    max-height: 360px;
  }

  .map-nav-result {
    padding: 15px;

    &__item {
      margin-bottom: 20px;
    }

    &__link {
      font-size: 18px;
    }

  }

}

